.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --main-font: "DM Sans", sans-serif;
  --secondary-font: "Titan One", cursive;
  --primary-text-color: #000;
  --secondary-text-color: #907e82;
  --third-text-color: #fff;
  --brand-color: #ffa5ba;
  --accent-color: #d41443;
  --header-background: #ffb8ca;
  --header-menu: rgba(225, 122, 146, .7);
  --animation-function: cubic-bezier(.4, 0, .2, 1);
}

.header {
  background-color: var(--header-background);
  overflow: hidden;
}

.header__nav {
  z-index: 3;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: fixed;
  top: 8px;
  left: 20px;
}

.header__link {
  display: inline-flex;
}

.header__logo {
  width: 199px;
  height: 50px;
}

.header__switcher {
  z-index: 5;
  width: 20px;
  height: 20px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 20px;
  right: 20px;
}

.header__icon {
  background-color: #00f;
}

.header__open {
  width: 20px;
  height: 12px;
}

.header__close {
  width: 14px;
  height: 14px;
}

.header__menu {
  height: 100vh;
  width: 100vw;
  z-index: 4;
  opacity: 0;
  pointer-events: none;
  background-color: var(--header-menu);
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  transition: opacity 250ms var(--animation-function), transform 250ms var(--animation-function);
  position: absolute;
  top: -8px;
  left: 0;
  transform: translateX(100%);
  box-shadow: 0 8px 32px rgba(31, 38, 135, .37);
}

.header__menu.is-open {
  opacity: 1;
  pointer-events: all;
  transform: translateX(-20px);
}

.header__list {
  flex-direction: column;
  margin: 20px;
  display: flex;
}

.header__order {
  text-align: center;
  max-width: 210px;
  height: 44px;
  transition: background-color 250ms var(--animation-function), color 250ms var(--animation-function), transform 250ms var(--animation-function);
  border-radius: 22px;
  align-items: center;
  margin-top: 60px;
  margin-bottom: 19px;
  padding-left: 48px;
  padding-right: 48px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: inline-flex;
}

.header__order:hover, .header__order:focus {
  background-color: var(--accent-color);
  color: var(--third-text-color);
  transform: scale(1.15);
}

.header__arrow {
  width: 8px;
  height: 10px;
  background-color: var(--accent-color);
  transition: background-color 250ms var(--animation-function);
  margin-left: 16px;
}

.header__order:hover .header__arrow, .header__order:focus .header__arrow {
  background-color: var(--third-text-color);
}

.header__mobile-item {
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
  display: inline-flex;
}

.header__mobile-item:nth-child(n) {
  border-top: 1px solid rgba(225, 132, 154, .7);
}

.header__mobile-item:last-child {
  border-bottom: 1px solid rgba(225, 132, 154, .7);
}

.header__anchor {
  height: 42px;
  width: 100%;
  color: var(--third-text-color);
  transition: color 250ms var(--animation-function), transform 250ms var(--animation-function);
  align-items: center;
  display: flex;
}

.header__anchor:hover, .header__anchor:focus {
  color: var(--accent-color);
  transform: scale(1.05);
}

